import React from 'react'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import Nav from './components/Nav'

import HomeScreen from './scenes/HomeScreen'
import SinglePageScreen from './scenes/SinglePageScreen'
import WebSiteScreen from './scenes/WebSiteScreen'
import PageDemoScreen from './scenes/PageDemoScreen'
import WebsiteDemoScreen from './scenes/WebsiteDemoScreen'

function App() {
  return (
    <Router>
      <Nav />
      <main>
        <Route path="/single-page-demo/:pageImgId" component={PageDemoScreen} />
        <Route path="/website-page-demo/:pageImgId" component={WebsiteDemoScreen} />
        <Route path="/single-pages" component={SinglePageScreen} exact />
        <Route path="/website-pages" component={WebSiteScreen} exact />
        <Route path="/website-demo" component={HomeScreen} exact />
        <Route path="/" component={HomeScreen} exact />
      </main>
    </Router>
  )
}

export default App
