import React from 'react'
import { useHistory } from 'react-router-dom'

const Nav = () => {
  const history = useHistory()

  const changeRouter = (router) => {
    history.push(router)
  }

  return (
    <nav className="nav">
      <article className="nav__container">
        <section className="nav__title">
          <span onClick={() => changeRouter('/website-demo')}>I-noby 頁面展示</span>
        </section>
        <div className="nav__link">
          <span onClick={() => changeRouter('/single-pages')}>單頁式網頁展示</span>
          <span onClick={() => changeRouter('/website-pages')}>多頁式網站展示</span>
        </div>
      </article>
    </nav>
  )
}

export default Nav
