import React from 'react'
import { useHistory } from 'react-router-dom'
import Illustration from '../svg/Illustration'

const Header = () => {
  const history = useHistory()

  const changeRouter = (router) => {
    history.push(router)
  }
  return (
    <header className="header">
      <article className="header__container">
        <section className="header__content">
          <h1 className="header__title">請點選所需介面以查看完整頁面</h1>
          <div className="header__link">
            <span className="line-btn line-btn--glassmorphism" onClick={() => changeRouter('/single-pages')}>
              單頁式網頁展示
            </span>
            <span className="line-btn" onClick={() => changeRouter('/website-pages')}>
              多頁式網站展示
            </span>
          </div>
        </section>
        <section className="header__illustration">
          <Illustration />
        </section>
      </article>
    </header>
  )
}

export default Header
