import React from 'react'
import { useHistory } from 'react-router-dom'
import { websiteTemplates } from '../constants/data'
import Search from '../svg/Search'

const WebSiteScreen = () => {
  const history = useHistory()

  const changeRouter = (router) => {
    history.push(router)
  }

  return (
    <>
      <header className="header-screen"></header>
      <section id="webSite" className="section">
        <article className="section__container">
          <span className="section__title section__title--bottom-line margin-bottom">多頁式網站展示</span>
          <ul className="website-demo">
            {websiteTemplates.map(({ url, demoImage, projectName, label }) => (
              <li key={url} onClick={() => changeRouter(`/website-page-demo/${projectName}`)}>
                <img src={demoImage} alt={projectName} />
                <span className="website-demo__view">
                  <h2 className="website-demo__title">{label}</h2>
                  <Search />
                </span>
              </li>
            ))}
          </ul>
        </article>
      </section>
    </>
  )
}

export default WebSiteScreen
