import React from 'react'
import { useHistory } from 'react-router-dom'
import { images, websiteTemplates } from '../constants/data'
import Header from '../components/Header'
import Search from '../svg/Search'

const HomeScreen = () => {
  const history = useHistory()

  const changeRouter = (router) => {
    history.push(router)
  }

  return (
    <>
      <Header />
      <section id="singlePage" className="section">
        <article className="section__container">
          <span className="section__title section__title--bottom-line margin-bottom">單頁式網頁展示</span>
          <ul className="single-pages-demo">
            {images.map(({ demoImage, alt, url }) => (
              <li key={alt} onClick={() => changeRouter(`/single-page-demo/${url}`)}>
                <img src={demoImage} alt={alt} />
                <span className="single-pages-demo__view">
                  <Search />
                </span>
              </li>
            ))}
          </ul>
        </article>
      </section>
      <section id="webSite" className="section">
        <article className="section__container">
          <span className="section__title section__title--bottom-line margin-bottom">多頁式網站展示</span>
          <ul className="website-demo">
            {websiteTemplates.map(({ url, demoImage, projectName, label }) => (
              <li key={url} onClick={() => changeRouter(`/website-page-demo/${projectName}`)}>
                <img src={demoImage} alt={projectName} />
                <span className="website-demo__view">
                  <h2 className="website-demo__title">{label}</h2>
                  <Search />
                </span>
              </li>
            ))}
          </ul>
        </article>
      </section>
    </>
  )
}

export default HomeScreen
